.productList{
    flex: 4;
    margin: 0.5%;
}

.productListItem{
    display: flex;
    align-items: center;
    text-align: center;
}

.productListImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
  }

.productListEdit{
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    background-color: teal;
    color: white;
    cursor: pointer;
    margin-right: 20px;
}

.productListDelete{
    color: red;
    cursor: pointer;
}

.MuiDataGrid-columnsContainer {
    background-color: teal;
    color: white;
}